import 'source-map-support/register';
import Express from 'express';
import _ from 'lodash';

import Config from '../config';

import * as AllRoutes0 from './routes/default.js'; import * as AllRoutes1 from './routes/graphiql.js'; import * as AllRoutes2 from './routes/graphql.js'; import * as AllRoutes3 from './routes/statics.js'; let AllRoutes = [AllRoutes0, AllRoutes1, AllRoutes2, AllRoutes3];

const app = new Express();

const init = async () => {

  //Server route definations
  _.each(
    _.sortBy( AllRoutes, [
      ({ route, url }) => {
        return url;
      }
    ] ).reverse(),
    ( { route, url } ) => {
      if ( url ) {
        app.use( url, route );
      } else {
        app.use( route );
      }
    }
  );
  //----------------------
};

init().then( () => {
  app.listen( Config[ 'webapp_port' ], () => {
    console.log( 'Application running', `PORT: ${Config['webapp_port']}` )
  } );
} );
